import { Router, ActivatedRoute } from '@angular/router';
import { Utils } from './../../services/utils.service';
import { MainService } from '../../services/main.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { timer } from 'rxjs';
import { Location } from '@angular/common';

@Component({
    selector: 'app-search',
    templateUrl: './search.component.html',
    styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {

    @ViewChild("search") inputSearch;

    public paramsUrl;
    public queryParamsUrl;

    carregantDiscover = true;
    // carregantSearch = true;
    carregantUsuaris = true;
    carregantMediums = true;
    carregantMovements = true;
    carregantTags = true;
    carregantPublicacions = true;

    public textSearch = "";
    public resultats;
    public discover;
    public imagesReady = [];
    public timeoutRef;

    constructor(
        public main: MainService,
        private router: Router,
        private route: ActivatedRoute,
        private location: Location
    ) {
        main.newPage(this);

        this.resultats = {};

        // force route reload whenever params change;
        this.router.routeReuseStrategy.shouldReuseRoute = () => false;
        this.route.params.subscribe(params => this.paramsUrl = params);
        this.route.queryParams.subscribe(params => this.queryParamsUrl = params);

        this.resultats.usuaris = [];
        this.resultats.mediums = [];
        this.resultats.movements = [];
        this.resultats.tags = [];
        this.resultats.publicacions = [];
    }



    async ngOnInit() {
        this.textSearch = this.queryParamsUrl.t || "";

        this.search();

        setTimeout(() => {
            this.inputSearch.nativeElement.focus();
        }, 500); // 500 s la duraci de la transici del .page-body //
    }
 
    ngOnDestroy() {
        clearTimeout(this.timeoutRef);
        this.main.onDestroyPage();
    }

    public modificarUrl() {
        let text = "/search";

        if (this.textSearch)
            text += `?t=${this.textSearch}`;
        
        // &medium bla bla bla...

        this.location.replaceState(text);
    }

    public search_delay(event, delay = 1000) {
        this.textSearch = event.target.value;

        clearTimeout(this.timeoutRef);
        this.timeoutRef = setTimeout(() => {
            this.search();
        }, delay);
    }

    public async search() {
        this.modificarUrl();

        if (!this.textSearch) {
            this.carregantDiscover = true;
            await this.getDiscoverPosts();
            this.carregantDiscover = false;
        } else {
            // this.carregantSearch = true;
            this.searchUsuaris();
            this.searchMediums();
            this.searchMovements();
            this.searchTags();
            this.searchPublicacions();
            // this.carregantSearch = false;
        }
    }
    async searchUsuaris() {
        this.carregantUsuaris = true;
        this.resultats.usuaris = await this.main.provider.searchUsuaris(this.textSearch);
        this.carregantUsuaris = false;
    }
    async searchMediums() {
        this.carregantMediums = true;
        this.resultats.mediums = await this.main.provider.searchMediums(this.textSearch);
        this.carregantMediums = false;
    }
    async searchMovements() {
        this.carregantMovements = true;
        this.resultats.movements = await this.main.provider.searchMovements(this.textSearch);
        this.carregantMovements = false;
    }
    async searchTags() {
        this.carregantTags = true;
        this.resultats.tags = await this.main.provider.searchTags(this.textSearch);
        this.carregantTags = false;
    }
    async searchPublicacions() {
        this.carregantPublicacions = true;
        this.resultats.publicacions = await this.main.provider.searchPublicacions(this.textSearch, 1, 10);
        this.carregantPublicacions = false;
    }


    public async getDiscoverPosts() { 

        let imagesConfig = [];
        this.discover = await this.main.provider.getXPosts(1, 100); 
        //ajustar array a la informacio necessaria
        // await this.discover.forEach((element, index) => { 
        //     imagesConfig.push({ idPost: element.idPost, type: element.content[0].type, url: element.content[0].url, alt: `#${index}`});
        // });

        this.imagesReady = imagesConfig;
        // this.imagesReady = this.shuffleArray(imagesConfig);
    }
    shuffleArray(array) {
        var m = array.length, t, i;
        while (m) {
            i = Math.floor(Math.random() * m--);
            t = array[m];
            array[m] = array[i];
            array[i] = t;
        }
        return array;
    }

    //Cooldowns
    public delay(seg: number) { 
        return new Promise( resolve => setTimeout(resolve, seg) );
    }

    public discoverDelay() { 
        timer(1000).subscribe(value => this.carregantDiscover = false);
    }


}

