import { Component, HostListener, Input, OnInit, ViewChild } from '@angular/core';
import { MasonryItem } from '../../../classes/masonryItem';

@Component({
    selector: 'app-discover',
    templateUrl: './discover.component.html',
    styleUrls: ['./discover.component.scss']
})
export class DiscoverComponent implements OnInit {

    carregant: boolean;
    userWindow: number;
    @ViewChild('table') table;
    @Input() posts: MasonryItem[];
    @Input() cols: number = 4;
    @Input('cols.xs') cols_xs: number; //col < 576px
    @Input('cols.sm') cols_sm: number; //col >= 576px
    @Input('cols.md') cols_md: number; //col >= 768px
    @Input('cols.lg') cols_lg: number; //col >= 992px
    @Input('cols.xl') cols_xl: number; //col >= 1200px
    @Input() rowHeight: number = 1;
    @Input() gutterSize: number = 10;

    constructor() { }

    ngOnInit(): void { }

    getDefaultSize() {

        var usersWidth = window.innerWidth;
    
        if (usersWidth < 576)
            return this.cols_xs;
        else if (usersWidth < 768)
            return this.cols_sm;
        else if (usersWidth < 992)
            return this.cols_md;
        else if (usersWidth < 1200)
            return this.cols_lg;
        else
            return this.cols_xl;
    }


}
