import {Component, ElementRef, HostListener, Input, ViewChild} from '@angular/core';
import { MasonryItem } from '../../../../classes/masonryItem';
import { MainService } from '../../../../services/main.service';

@Component({
    selector: 'app-grid-item',
    templateUrl: './grid-item.component.html',
    styleUrls: ['./grid-item.component.scss']
})
export class GridItemComponent {

    @Input() post: MasonryItem;
    @Input() rowHeight: number = 1;
    @Input() gutterSize: number = 1;
    @ViewChild('masonryPost') masonryPost: ElementRef;

    public rows: number = 0;

    constructor(public main: MainService) { }

    getRows() {
        return Math.floor(this.masonryPost?.nativeElement.offsetHeight / (this.rowHeight + this.gutterSize));
    }

}
